.learnHeader{
  font-family: 'Merriweather Sans', sans-serif;    
  color: rgb(255, 255, 255);
  font-size: 50px;
  font-weight: 700px;
  text-align: center;
  letter-spacing: 5px;
  padding: 30px 0 8px;
  position: relative;
}

.learnHeader::before {
  content: "";
  position: absolute;
  left: 7%;
  bottom: 16px;
  width: 150px;
  height: 14px;
  transform: skew(-12deg) translateX(-50%);
  background: rgba(117, 74, 238, 0.5);
  z-index: -1;
}

.university::before {
  content: "";
  position: absolute;
  left: 7%;
  bottom: 16px;
  width: 150px;
  height: 14px;
  transform: skew(-12deg) translateX(-50%);
  background: rgba(117, 74, 238, 0.5);
  z-index: -1;
}

.aboutUsList {
  font-family: 'Poppins',sans-serif;
  border-right: solid 3px rgba(158, 110, 212, 0.75);
  white-space: nowrap;
  overflow: hidden;    
  font-size: 40px;
  color: rgba(255, 255, 255, 0.7);
  margin: 0 auto;
  font-weight: bold;
}

/* Animation */
.aboutUsList {
  animation: animated-text 4s steps(29,end) 0s 1 normal both,
             animated-cursor 600ms steps(29,end) infinite;
}

/* text animation */

@keyframes animated-text{
  from{width: 0;}
  to{width: 472px;}
}

/* cursor animations */

@keyframes animated-cursor{
  from{border-right-color: rgba(98, 89, 218, 0.75);}
  to{border-right-color: transparent;}
}


