.howItWorks {
  border-radius: 2rem;
  background-color:#78bce4;
  border:rgb(136, 137, 224);
  padding: 20px;
  width: 200px;
  transition: all 0.5s;
  font-size: 25px;
  color: white;
  font-family: 'Poppins', sans-serif;
  display: inline-block;
  position: relative;
  transition: 0.5s;
  margin-left: 575px;
  margin-top: 50px;
  justify-content: center;
  text-decoration: none;

}

.howItWorks:hover{
    font-size: 30px;
    background-color: rgb(136, 137, 224);
    padding-right: 24px;
    padding-left:8px;
    text-decoration: none;
}
.howItWorks:hover:after{
  opacity: 1;
  right: 10px;
  text-decoration: none;
}

.howItWorks:after{
  content: '»';
  position: absolute;
  opacity: 0;  
  top: 20px;
  right: -10px;
  transition: 0.1s;
  height: 10px;
  width: 10px;
  text-decoration: none;
}




.one, .two, .three {
  overflow: hidden;    
  font-family: 'Poppins', sans-seriff;  
  font-size: 50px;
  color: rgb(238, 238, 238);
  font-weight: bold;
}

.list {
  counter-reset: item;
  list-style-type: none;
  line-height: 2.2;
  margin-left: -40px;
  text-align: center;
}

.list li {
  display: block;
  position: relative;

}

.list li span {
  margin-left: 40px;
}

ol li:before {
  content: counter(item) " ";
  counter-increment: item;
  color: #ffffff;
  position: absolute;
  top: 50%;
  margin-top: -4em;
  margin-left: -10em;
  background: #747ceb;
  height: 7em;
  width: 7em;
  line-height: 7em;
  font-size: 11px;
  text-align: center;
  border-radius: 7em;
  font-weight: 1000;
}
