
.bGround {
    background-color: #cdcdcd;
    min-height: 100vh;
  }
  
  .contactForm{
      background-color: #d6c488;
      padding: 10px;
      margin-top: 30px;
      margin-left: 400px;
      text-align: center;
      border-radius: 25px;
      width: 625px;
      height: 660px;
     
  }
  
  .contactFormBody {
      background-color: white;
      text-align: left;
      border-radius: 25px;
      width: 625px;
      height: 465px;
      justify-content: center;
  
  }
  .contactHeader {
      font-family: 'Merriweather Sans', sans-serif;    
      font-size: 50px;
      color: white;
      text-align: center;
      
      
  }
  
  .contactInfo {
      font-family: 'Poppins',sans-serif;
      font-size: 20px;
      color: white;
      text-align: center;
  }
  
  
  ::placeholder{
    color:#4791fd;
    font-size: 20px;
    padding: 10px;
  }
  
  .firstName{
      display: inline-block;
      width: 200px;
      text-align: left;
      background: transparent;
      outline: none;
      margin-left: 75px;
      margin-top: 50px;
      color:#4791fd;
      border:none;
      border-bottom: 1px solid #4791fd;
      font-family: 'Poppins',sans-serif;
      font-size: 20px;
  
  }
  
  
  .lastName {
    display: inline-block;
    width: 200px;
    text-align: left;
