  .submitBtn{
    border-radius: 1rem;
    background-color:#4791fd;
    border:rgb(136, 137, 224);
    width: 150px;
    height: 50px;
    transition: all 0.5s;
    font-size: 25px;
    color: 4791fd;
    font-family: 'Poppins', sans-serif;
    display: inline-block;
    position: relative;
    transition: 0.5s;
    margin-left: 220px;
    margin-bottom: 20px;
    margin-top: 40px;
    justify-content: center;
    text-decoration: none;
    color: #ffffff;
   
  
  }
  
  .submitBtn:hover{
      font-size: 30px;
      opacity: .6;
      text-decoration: none;
  }